<template>
  <b-poptip
    ref="searchPoptip"
    trigger="focus"
    :theme="getTheme"
    placement="bottom-start"
    max-length="400px"
  >
    <b-input
      v-model="keywords"
      search
      @search="goSearchDetailPage"
      @keyupEnter="goSearchDetailPage"
    />
    <template v-slot:content>
      <hot-search-list v-show="!isSearch" @hidden="closePopover" />
      <search-suggest
        :keywords="keywords"
        v-show="isSearch"
        @hidden="closePopover"
      />
    </template>
  </b-poptip>
</template>

<script>
import { theme } from "mixin/global/theme.js";
import HotSearchList from "./hot-search-list";
import SearchSuggest from "./search-suggest";
export default {
  mixins: [theme],
  components: { HotSearchList, SearchSuggest },
  data() {
    return {
      keywords: "",
      isSearch: false, //
    };
  },
  methods: {
    goSearchDetailPage() {
      this.$router.push(`/search-detail/${this.keywords}`);
      this.$refs.searchPoptip.hidden();
    },
    closePopover() {
      this.$refs.searchPoptip.hidden();
    },
  },
  watch: {
    /** */
    keywords() {
      if (this.keywords != "" && !this.isSearch) {
        this.isSearch = true;
      }
      /**isSearchtrueisSearchfalse */
      if (this.keywords == "" && this.isSearch) {
        this.isSearch = false;
      }
    },
  },
};
</script>

